@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: local("PT Sans"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-style: bold;
  font-weight: 700;
  src: local("PT Sans Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
}
.page__body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  text-transform: uppercase;
  min-width: 320px;
  min-height: 100%;
  width: auto;
  margin-left: auto;
  margin-right: auto;
}
.page__container {
  background-color: #E7E5E5;
  min-width: 320px;
  min-height: 100%;
  width: auto;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-rows: max-content 1fr min-content;
  align-content: start;
}
@media (min-width: 1200px) {
  .page__container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
.header {
  background-color: #FFFFFF;
  position: relative;
  min-height: 56px;
}
@media (min-width: 768px) {
  .header {
    max-height: 56px;
  }
}
.header__home-link {
  position: absolute;
  right: 0;
  left: 0;
  bottom: -30px;
  width: 102px;
  height: 84px;
  margin-right: auto;
  margin-left: auto;
}
.site-navigation__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  background-color: #81B3D2;
  min-height: 223px;
  position: relative;
  top: -54px;
  margin-top: 54px;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .site-navigation__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    position: static;
    background-color: transparent;
    color: black;
    min-height: auto;
    margin-top: 0px;
  }
}
.site-navigation__link {
  font-weight: 700;
  font-size: 18px;
  line-height: 40px;
  color: inherit;
}
.site-navigation__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  box-shadow: 0px 1px 0px rgba(1, 1, 1, 0.2), inset 0px 1px 0px rgba(255, 255, 255, 0.2);
  min-height: 55px;
  min-width: 150px;
}
@media (min-width: 768px) {
  .site-navigation__item {
    box-shadow: none;
  }
}
.site-navigation__item:nth-child(3) {
  grid-column: 4;
}
.site-navigation__dropdown {
  width: 23px;
  height: 23px;
  border: none;
  background-color: transparent;
  position: absolute;
  right: 20px;
  bottom: 15px;
}
@media (min-width: 768px) {
  .site-navigation__dropdown {
    display: none;
  }
}
.site-navigation__dropdown--burger {
  background-image: url("../img/icon-menu-burger.svg");
  background-repeat: no-repeat;
}
.button {
  background-color: #81B3D2;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  color: #FFFFFF;
  display: block;
  padding: 15px;
  text-align: center;
  border: none;
  cursor: pointer;
  text-transform: inherit;
}
.main {
  background-color: #FFFFFF;
}
.main-header {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;
  background-repeat: no-repeat, no-repeat;
  background-position: center 40px, top;
}
.main-header--index-page {
  background-image: url("../img/text-welcome-sedona etc.svg"), url("../img/mount-back-welcome-section.jpg");
  background-size: 90% 50%,
    100% 70%;
  min-height: 630px;
}
.main-header--inner-page {
  background-image: url("../img/SEDONA.svg"), url("../img/mount-back-welcome-section.jpg");
  min-height: 630px;
}
.main-header__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}
.main-header__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 26px;
  color: #333333;
}
.benefits__list {
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .benefits__list {
    display: flex;
    flex-wrap: wrap;
  }
}
.benefits__item {
  background-color: #EEEEEE;
  min-height: 168px;
  display: flex;
  flex-direction: column;
  text-align: center;
  flex-grow: 1;
}
@media (min-width: 768px) {
  .benefits__item {
    flex-direction: row;
  }
}
.benefits__container {
  min-height: 211px;
  flex-grow: 1;
}
@media (min-width: 768px) {
  .benefits__container {
    max-width: 33.33%;
    min-height: 256px;
  }
}
.benefits__item--big {
  background-color: #81B3D2;
}
@media (min-width: 768px) {
  .benefits__item--big {
    flex-direction: row;
  }
}
@media (min-width: 768px) {
  .benefits__item--big-reverse {
    flex-direction: row-reverse;
  }
}
.benefits__item--big::after {
  content: "";
  min-height: 260px;
  flex-grow: 1;
}
@media (min-width: 768px) {
  .benefits__item--big::after {
    min-width: 512px;
  }
}
@media (min-width: 1200px) {
  .benefits__item--big::after {
    min-width: 800px;
  }
}
.benefits__item--street-photo.benefits__item--big::after {
  background-image: url("../img/street-benefits-section.jpg");
  background-repeat: no-repeat;
  background-position: center;
}
.benefits__item--bridge-photo.benefits__item--big::after {
  background-image: url("../img/devils-bridge-benefits-section.jpg");
  background-repeat: no-repeat;
}
.benefits__item--big .benefits__title,
.benefits__item--big .benefits__text,
.benefits__item--big .benefits__number {
  color: #FFFFFF;
}
.benefits__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  margin-top: 40px;
}
.benefits__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
}
.benefits__number {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
}
.entertainments__title {
  margin-top: 40px;
}
.entertainments {
  background-color: #FFFFFF;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .entertainments {
    flex-direction: row;
  }
}
.entertainments__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}
.entertainments__title--accomadation {
  background-image: url("../img/icon-house.svg");
  background-repeat: no-repeat;
}
.entertainments__title--food {
  background-image: url("../img/icon-food.svg");
  background-repeat: no-repeat;
}
.entertainments__title--suvenirs {
  background-image: url("../img/icon-suvenirs.svg");
  background-repeat: no-repeat;
}
.entertainments__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
}
.booking {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  min-height: 287px;
  align-items: center;
  text-align: center;
}
.booking__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #333333;
}
.booking__subject {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
}
.booking__button {
  min-width: 250px;
}
.map {
  background-image: url("../img/map-marker.svg"), url("../img/map-back-map-section.jpg");
  background-repeat: no-repeat, no-repeat;
  background-position: center;
  min-width: 320px;
  min-height: 350px;
}
.footer {
  background-color: #FFFFFF;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
}
.hash__item {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
}
.social {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}
.social__item {
  display: block;
  position: relative;
  width: 48px;
  height: 48px;
  background-color: #81B3D2;
  margin-right: 6px;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}
.social__item:last-child {
  margin-right: 0;
}
.social__item--twitter {
  background-image: url("../img/social/icon-twitter.svg");
}
.social__item--facebook {
  background-image: url("../img/social/icon-facebook.svg");
}
.social__item--youtube {
  background-image: url("../img/social/icon-youtube.svg");
}
.developer {
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
}
.developer__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 26px;
}
.review-form {
  text-align: center;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .review-form {
    flex-direction: row;
    flex-wrap: wrap;
  }
}
@media (min-width: 768px) {
  .review-form__fieldset--impression {
    order: -1;
    flex-grow: 1;
  }
}
@media (min-width: 768px) {
  .review-form__fieldset--name {
    flex-grow: 1;
  }
}
@media (min-width: 768px) {
  .review-form__fieldset--textarea,
  .review-form__fieldset--places,
  .review-form__fieldset--contact {
    width: 100%;
  }
}
.review-form__legend {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
}
.review-form__label {
  font-weight: 400;
  font-size: 14px;
  line-height: 26px;
  color: #333333;
  display: flex;
  flex-wrap: wrap;
}
.review-form__input {
  background-color: #F2F2F2;
  border: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}
.review-form__input::placeholder {
  color: #000000;
}
.review-form__label--contacts {
  position: relative;
}
.review-form__label--phone::after {
  content: " ";
  position: absolute;
  display: block;
  width: 58px;
  height: 58px;
  right: 50px;
  background-color: #E6E6E6;
  background-image: url("../img/icon-phone.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.review-form__label--email::after {
  content: " ";
  position: absolute;
  display: block;
  width: 58px;
  height: 58px;
  right: 50px;
  background-color: #E6E6E6;
  background-image: url("../img/icon-email.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.review-form__label--questionnaire {
  display: flex;
  flex-direction: row-reverse;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: none;
}
.review-form__textarea {
  width: 100%;
}
.review-form__textarea::placeholder {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}
.review-form__popup {
  text-align: center;
}
.popup__subject {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}
.popup__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
}
.popup {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.photo-gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  outline: 5px solid rgba(9, 214, 53, 0.3);
  outline-offset: -5px;
}
.photo {
  margin: 0;
  padding: 0;
}
.photo__thumbnail {
  margin: 0;
  padding: 0;
}
.photo__title {
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
}
.photo__author {
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #333333;
  text-transform: none;
}
.photo__likes {
  display: flex;
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
}
.photo__likes::before {
  content: "";
  display: block;
  width: 23px;
  height: 23px;
  background-image: url("../img/icon-like.svg");
  background-repeat: no-repeat;
}
.photo__item {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}
.video-gallery {
  text-align: center;
  outline: 5px solid rgba(253, 249, 0, 0.363);
  outline-offset: -5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.video-gallery__subject {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  margin: 0;
  padding: 0;
}
.video-gallery__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #333333;
}
.video-interface-range {
  background-color: rgba(0, 0, 0, 0.6);
}
